import { OldCounterItem } from "./OldCounterItem";
import { NodesRecord, Node, IOM } from "../Utils";
import { SyntheticEvent, useState } from 'react';
import { Collapse } from 'reactstrap';

interface IOptions {
    name: keyof IOM;
    data: IOM | NodesRecord;
}

export const OldCounterFolder = ({ name, data }: IOptions) => {
    const [isOpen, setIsOpen] = useState(false);
    const onClick = (evt: SyntheticEvent) => {
        evt.stopPropagation();
        setIsOpen(value => !value);
    };

    return (
        <div key={ name } className='bg-dark pt-1 pb-1 pl-3 pr-3'>
            <div className='text-white' onClick={ onClick }>{ name }</div>

            <Collapse isOpen={ isOpen }>
                { Object.keys(data).map(k => (
                    data[k].isNode
                        ? <OldCounterItem data={ data[k] as Node } key={ k } />
                        : <OldCounterFolder data={ data[k] as IOM } name={ k } key={ k } />
                )) }
            </Collapse>
        </div>
    );
}
