import { useState } from 'react';
import { IState } from "../Utils";

type UseLocalStorage = ({ keys, initialState }: { keys: Array<keyof IState>, initialState?: IState}) => [Partial<IState>, Function];
type ISetStorageProps = ((state: IState) => IState) | IState;


export const useLocalStorage: UseLocalStorage = ({ keys, initialState = {} }) => {
   const commonState = {} as IState;
   const initialKeys = Object.keys(initialState) as Array<keyof IState>;

   initialKeys.forEach(k => {
      localStorage.setItem(k, JSON.stringify(initialState[k]));
   });

   keys.forEach(k => {
      const record = JSON.parse(localStorage.getItem(k) as string);

      if (record !== null && record !== undefined) {
         commonState[k] = record;
      }
   });

   const [state, setState] = useState(commonState);

   const storageSetState = (newState: ISetStorageProps) => {
      let resultState: IState;

      if (typeof newState === 'function') {
         resultState = { ...state, ...newState(state) };
      } else {
         resultState = { ...state, ...newState };
      }

      keys.forEach(k => localStorage.setItem(k, JSON.stringify(resultState[k])));
      setState(resultState);
   };

   return [state, storageSetState];
}
