import { AppContext } from "../Contexts/AppContext";
import { Col, Row } from "reactstrap";
import { useContext } from 'react';
import { IOM, NodesRecord } from "../Utils";
import { OldCounterFolder } from "./OldCounterFolder";

export const OldCounters = () => {
   const [state] = useContext(AppContext);
   const result = {} as IOM;

   const createStructure = () => {
      state.oldCounters.ids.forEach(id => {
         const date = new Date(+id);
         const month = String(date.toLocaleString('default', { month: 'long' }));
         const dateCreated = String(date.getDate());

         if (!result[month]) {
            result[month] = {};
         }

         if (!(result[month] as IOM)[dateCreated]) {
            (result[month] as IOM)[dateCreated] = {};
         }

         ((result[month] as IOM)[dateCreated] as NodesRecord)[id] = {
            ...state.oldCounters.data[id], id: id, isNode: true
         };
      });
   }

   createStructure();

   if (!state.oldCounters.ids.length) {
      return <></>
   }

   return (
      <Row className='overflow-auto'>
         <Col className='mb-5'>
            { Object.keys(result).map(k => (
               <OldCounterFolder data={ result[k] as IOM } name={ k } key={ k } />
            )) }
         </Col>
      </Row>
   )
};
