import {
   getNewTime,
   counterInitialValue,
   CounterStates,
   Modes,
   IState,
   IAction,
   CounterTypes, ICounterItem
} from "../Utils";
import { Actions } from '../Actions';

export const rootReducer = (state: IState, action: IAction): IState => {
   let newState: IState;

   switch (action.type) {
      case Actions.STOP:
         if (state.counters.data[action.id].state === CounterStates.Playing) {
            clearInterval(state.counters.data[action.id].intervalId);
         }

         return {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     state: CounterStates.Pause,
                     value: counterInitialValue.value
                  }
               }
            }
         }
      case Actions.PLAY:
         if (state.mode === Modes.Single) {
            const countersData = {} as IState[CounterTypes]['data'];

            state.counters.ids.forEach(id => {
               countersData[id] = {
                  ...state.counters.data[id],
                  state: CounterStates.Pause
               }

               if (countersData[id].intervalId !== null) {
                  clearInterval(countersData[id].intervalId);
                  countersData[id].intervalId = null;
               }
            });


            return {
               ...state,
               counters: {
                  ...state.counters,
                  data: {
                     ...countersData,
                     [action.id]: {
                        ...state.counters.data[action.id],
                        state: CounterStates.Playing,
                     }
                  }
               }
            }
         } else {
            return {
               ...state,
               counters: {
                  ...state.counters,
                  data: {
                     ...state.counters.data,
                     [action.id]: {
                        ...state.counters.data[action.id],
                        state: CounterStates.Playing,
                     }
                  }
               }
            }
         }
      case Actions.PAUSE:
         clearInterval(state.counters.data[action.id].intervalId);

         return {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     state: CounterStates.Pause
                  }
               }
            }
         }
      case Actions.UPDATE_TIME:
         newState = {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     value: getNewTime(state.counters.data[action.id].value)
                  }
               }
            }
         };

         action.setStorageState({
            ...newState,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     state: CounterStates.Pause
                  }
               }
            }
         });

         return newState;
      case Actions.SET_INTERVAL_ID:
         newState = {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     intervalId: action.intervalId
                  }
               }
            }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.CREATE_COUNTER:
         const newId = String(new Date().getTime());
         state.counters.ids.push(newId);
         newState = {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [newId]: { ...counterInitialValue }
               }
            }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.DELETE_AND_MOVE_TO_TABLE:
         const ids = state.counters.ids.filter(id => id !== action.id);
         let oldCounter = {} as ICounterItem;
         const counterData = (({ [action.id]: counter, ...rest }) => {
            const { seconds = 0, minutes = 0, hours = 0 } = counter?.value;

            if (seconds !== 0 || minutes !== 0 || hours !== 0) {
               oldCounter = { ...counter };
               state.oldCounters.ids.push(action.id);
            }

            return rest;
         })(state.counters.data);

         if (state.counters.data[action.id].intervalId) {
            clearInterval(state.counters.data[action.id].intervalId);
         }

         newState = {
            ...state,
            oldCounters: {
               ...state.oldCounters,
               data: {
                  ...state.oldCounters.data,
                  [action.id]: oldCounter
               }
            },
            counters: { ids, data: counterData }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.CHANGE_NAME_FROM_EXIST:
         newState = {
            ...state,
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: {
                     ...state.counters.data[action.id],
                     name: action.payload
                  }
               }
            }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.CHANGE_NAME_FROM_DELETED:
         newState = {
            ...state,
            oldCounters: {
               ...state.oldCounters,
               data: {
                  ...state.oldCounters.data,
                  [action.id]: {
                     ...state.oldCounters.data[action.id],
                     name: action.payload
                  }
               }
            }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.DELETE_FROM_TABLE:
         const data = (({ [action.id]:_, ...rest }) => rest)(state.oldCounters.data);
         const newIds = state.oldCounters.ids.filter(id => action.id !== id);

         newState = {
            ...state,
            oldCounters: { ids: newIds, data }
         };

         action.setStorageState(newState);

         return newState;
      case Actions.CHANGE_MODE:
         if (action.mode !== state.mode) {
            newState = {
               ...state,
               mode: action.mode
            }

            action.setStorageState(newState);

            return newState;
         }

         return { ...state };
      case Actions.RESTORE_FROM_TABLE:
         const oldIds = state.oldCounters.ids.filter(id => id !== action.id);
         let newCounter = {} as ICounterItem;

         const oldCountersData = (({ [action.id]: counter, ...rest }) => {
            newCounter = { ...counter };
            state.counters.ids.push(action.id);

            return rest;
         })(state.oldCounters.data);

         newState = {
            ...state,
            oldCounters: { ids: oldIds, data: { ...oldCountersData } },
            counters: {
               ...state.counters,
               data: {
                  ...state.counters.data,
                  [action.id]: newCounter
               }
            }
         };

         action.setStorageState(newState);

         return newState;
      default:
         return { ...state };
   }
}
