import { AppContext } from "../Contexts/AppContext";
import { Actions } from "../Actions";
import { useContext } from "react";
import { Node } from "../Utils";

import './_oldCounterItem.css';

interface IOptions {
    data: Node;
}

export const OldCounterItem = ({ data }: IOptions) => {
    const [, dispatch] = useContext(AppContext);
    const { id, name, value: { hours, minutes, seconds } } = data;

    const onDeleteClick = (id: string) => dispatch({ type: Actions.DELETE_FROM_TABLE, id });
    const onRestoreClick = (id: string) => dispatch({ type: Actions.RESTORE_FROM_TABLE, id });
    const onChangeName = ({ target: { value } } : { target: HTMLInputElement }, id: string) => (
        dispatch({ type: Actions.CHANGE_NAME_FROM_DELETED, id, payload: value })
    );

    return (
        <div className='oldCounterItem d-flex justify-content-between p-3 border-bottom align-items-center'>
            <p className='oldCounterItem__item text-white m-0'>
                <input type='text'
                       className='text-white'
                       title={ name }
                       value={ name }
                       style={{ background: 'transparent', border: 'none' }}
                       onChange={(evt) => onChangeName(evt, id)}
                 />
            </p>
            <p className='oldCounterItem__item text-white m-0'>{ hours } : { minutes } : { seconds }</p>
            <p className='oldCounterItem__item oldCounterItem__item--action m-0' onClick={() => onRestoreClick(id)}>&#9874;</p>
            <p className='oldCounterItem__item oldCounterItem__item--action text-danger m-0' onClick={() => onDeleteClick(id)}>&times;</p>
        </div>
    );
};
