import { useState, useContext } from 'react';
import { Dropdown, DropdownToggle, DropdownItem, DropdownMenu } from "reactstrap";
import { AppContext } from "../Contexts/AppContext";
import { Modes as ModeTypes } from "../Utils";
import { Actions } from '../Actions';

export const Modes = () => {
   const [state, dispatch] = useContext(AppContext);
   const [isOpen, setIsOpen] = useState(false);

   return (
      <div style={{ position: 'fixed', top: '10px', right: '15px' }}>
         <Dropdown isOpen={ isOpen } toggle={ () => setIsOpen(value => !value) }>
            <DropdownToggle caret>{ state.mode }</DropdownToggle>
            <DropdownMenu container="body">
               <DropdownItem onClick={() => dispatch({ type: Actions.CHANGE_MODE, mode: ModeTypes.Single })}>
                  { ModeTypes.Single }
               </DropdownItem>
               <DropdownItem onClick={() => dispatch({ type: Actions.CHANGE_MODE, mode: ModeTypes.Multi })}>
                  { ModeTypes.Multi }
               </DropdownItem>
            </DropdownMenu>
         </Dropdown>
      </div>
   )
}
