import { useContext } from 'react';
import { CounterStates } from "../Utils";
import { Button } from "reactstrap";
import { AppContext } from "../Contexts/AppContext";
import { Actions } from "../Actions";

export const Counter = ({ id }: { id: string }) => {
   const [state, dispatch] = useContext(AppContext);
   const isPlaying = () => state.counters.data[id].state === CounterStates.Playing;
   const onPausePlayClick = () => {
      if (isPlaying()) {
         dispatch({ type: Actions.PAUSE, id });
      } else {
         dispatch({ type: Actions.PLAY, id });
         const intervalId = setInterval(() => dispatch({ type: Actions.UPDATE_TIME, id }), 1000);
         dispatch({ type: Actions.SET_INTERVAL_ID, intervalId, id });
      }
   };
   const onStopClick = () => dispatch({ type: Actions.STOP, id });
   const onDeleteClick = () => dispatch({ type: Actions.DELETE_AND_MOVE_TO_TABLE, id });
   const onNameChange = ({ target: { value } }: { target: HTMLInputElement }) => (
       dispatch({ type: Actions.CHANGE_NAME_FROM_EXIST, payload: value, id })
   );

   const { value: { hours, minutes, seconds }, name } = state.counters.data[id];

   return (
      <div style={{ border: '1px solid' }} className='d-flex flex-column position-relative pt-4 pb-4 bg-dark'>
         <div className='position-absolute text-danger'
              style={{ top: '-5px', right: '5px', fontSize: '20px', cursor: 'pointer' }}
              onClick={ onDeleteClick }>
            &times;
         </div>
         <input className='mb-3 mt-3 align-self-center text-white bg-dark'
                style={{ maxWidth: '60%', border: 'transparent' }}
                value={ name }
                title={ name }
                onChange={ onNameChange } />
         <div className='mb-5 justify-content-around d-flex'>
            <div className='text-white'>
               { hours } :
            </div>
            <div className='text-white'>
               { minutes } :
            </div>
            <div className='text-white'>
               { seconds }
            </div>
         </div>
         <div className='justify-content-around d-flex'>
            <Button color='success' onClick={ onPausePlayClick } style={{ flexBasis: '40%' }}>
               { isPlaying() ? 'Пауза' : seconds === 0 ? 'Старт' : 'Продолжить' }
            </Button>
            { !isPlaying() && <Button color='success' style={{ flexBasis: '40%' }} onClick={ onStopClick }>Стоп</Button> }
         </div>
      </div>
   )
};
