import { useReducer } from 'react';
import { OldCounters } from "./OldCounters";
import { Container } from 'reactstrap';
import { CreateCounter } from './CreateCounter';
import { rootReducer } from '../Reducers/RootReducer';
import { AppContext } from '../Contexts/AppContext';
import { useLocalStorage } from '../Hooks/useLocalStorage';
import { IState, initialState, IAction } from '../Utils';
import { Modes } from './Modes';
import { Counters } from "./Counters";

export const App = () => {
   const [storageState, setStorageState] = useLocalStorage({ keys: Object.keys(initialState) as Array<keyof IState> });
   const [state, dispatch] = useReducer<typeof rootReducer>(rootReducer, { ...initialState, ...storageState });
   const customDispatch = (data: Partial<IAction>) => dispatch({ ...data, setStorageState });

   return (
      <AppContext.Provider value={[state, customDispatch]}>
         <Container fluid className='bg-primary' style={{ minHeight: '100vh', padding: '70px 3rem 20px' }}>
            <Modes />
            <OldCounters key='oldCounters' />
            <Counters />
            <CreateCounter key='createCounter'/>
         </Container>
      </AppContext.Provider>
   )
};
