import { Actions } from './Actions';

export enum CounterStates { Playing, Pause }

export const counterInitialValue: ICounterItem = {
   name: 'без имени',
   state: CounterStates.Pause,
   value: {
      seconds: 0,
      minutes: 0,
      hours: 0
   },
   intervalId: null
};

export enum Modes { Single = 'single', Multi = 'multi' }

export const initialState: IState = {
   counters: {
      ids: [],
      data: {}
   },
   oldCounters: {
      ids: [],
      data: {}
   },
   mode: Modes.Multi
};

export const getNewTime = (state: ICounterValue) => {
   const { seconds, minutes, hours } = state;

   let newSeconds = seconds + 1;
   let newMinutes = minutes;
   let newHours = hours;

   if (newSeconds === 60) {
      newSeconds = 0;
      newMinutes += 1;
   }
   if (newMinutes === 60) {
      newMinutes = 0;
      newHours += 1;
   }

   return {
      seconds: newSeconds,
      minutes: newMinutes,
      hours: newHours
   }
}

export type CounterTypes = 'counters' | 'oldCounters';

export interface ICounterValue {
   hours: number,
   minutes: number,
   seconds: number
}

export interface ICounterItem {
   value: ICounterValue;
   state: CounterStates;
   intervalId: NodeJS.Timeout | null;
   name: string;
}

export type IState = {
   [K in CounterTypes]: {
      ids: string[],
      data: {
         [key: string]: ICounterItem
      }
   }
} & { mode: Modes }

export interface IAction {
   setStorageState: Function;
   id?: string;
   type?: Actions;
   payload?: string;
   intervalId?: NodeJS.Timeout;
   mode?: Modes;
}

export type Node = { isNode: boolean; id?: string } & ICounterItem;
export type NodesRecord = Record<string, Node>;
export interface IOM { [K: string]: Node | NodesRecord | IOM; }
